* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.country-list {
  max-width: fit-content;
  padding: 10px;
  list-style: none;
}

.country-item {
  opacity: 0;
  align-items: center;
  animation: fadeinout .25s linear forwards;
  display: flex;
}

@keyframes fadeinout {
  100% {
    opacity: 1;
  }
}

.listed-country {
  max-width: 400px;
  vertical-align: middle;
  background: rgba(33, 33, 33, .267);
  border-radius: 3px;
  align-items: center;
  padding: 10px;
  font-weight: 700;
}

.flag {
  width: 60px;
  height: 40px;
  border: 2px solid rgba(33, 33, 33, .176);
  border-radius: 3px;
  margin: 10px;
  box-shadow: 5px 5px 10px -6px #42445a;
}

